#map1 {
    margin: 0;
    padding: 0;
    width: 100%;
    /* height: 100%; */
    height: 50vh;
}
.mapboxgl-map {
    font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
    overflow: hidden;
    position: relative;
    height:50vh;
    border-radius: 5px;
}

body{
    margin: 0;
    padding: 0;
}

.main_1{
    margin: 0;
    padding: 0;
    
}

.container_body{
    box-sizing: border-box;
    display: flex;
    min-height: 100vh;
    height: auto;
   
}

.left_body{
    box-sizing: border-box;
    float: left;
    margin: 15px;
    width: 35%;
    border-radius: 5px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px;
    padding: 25px;
}

.left_body p{
    font-size: 18px;
}

.bloodBanksRecord{
    height: auto;
    max-height: calc(100vh - 50px);
    width: 100%;
    /* border: 1px solid black; */
    /* background-color: rgb(201, 201, 201); */
    padding: 0px;
    margin: 5px 0px;
    border-radius: 5px;
    overflow: hidden;
    overflow-y: scroll;
    scroll-behavior: smooth;
}

.bloodBanksRecord div{
    padding: 3px;
    height: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 2px;
}

.right_body{
    box-sizing: border-box;
    float: right;
    width: calc(100vw - 35%);
    margin: 15px;
    border-radius: 10px;
    background-color:white;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px;;
    padding: 25px;
}

.right_body p{
    font-size: 20px;
}

/* this is map */
.liveMap{
    height: 50vh;
    width: 100%;
    background-color: rgb(255, 255, 255);
}

.lower-right{
    position: sticky;
    
    overflow-x: scroll;
    padding: 10px;
    scroll-behavior: smooth;
    top: 73vh;
    display: flex;
    justify-content: space-around;
    margin: 5px 7px 0  7px;
}

.colm{
    height: 150px;
    width: 250px;
    padding: 10px;
    margin: 8px  4px;
    border-radius: 4px;
   background: #f6f7f9;
    
   box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px;
}

.bank1 span{
    margin: 5px;
}

.colm p{
    text-align: center;
    margin: 10px 10px;
    padding: 2px 2px;
    font-size: 12px;
    text-transform:capitalize;
}

h1,h2,h3{
    text-align: center;
    height: 20px;
    margin: 8px;
}

@media only screen and (max-width: 650px) {
    .container_body{
        display: flex;
        flex-direction: column;
        padding: 5px;
        position: relative;
        
    }
    .left_body{
        padding: 15px;
        width: auto;
        height: 350px;
        float: none;
    }
    .bloodBanksRecord{
        height: 150px;
        /* display: flex; */
        overflow: hidden;
        overflow-y: scroll;
    }

    .bloodBanksRecord div{
        margin: 10px;
        height: 35px;
    }
    .right_body{
        padding: 15px;
        width: auto;
        float: none;
    }

    .liveMap{
        height: 45vh;
    }
}