html, body * { box-sizing: border-box; }

body {
    background:
      linear-gradient(
      rgba(246,247,249,0.8),
      rgba(246,247,249,0.8));
  }
  .main{
    display: none;
    
    position: fixed;
    z-index: 100; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: hidden;
    
    background-color: rgba(13, 14, 17, 0.936); 
    padding-top: 60px;
    

  }
  .close {
    position: absolute;
    right: 25px;
    top: 0;
    color:white;
    font-size: 35px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: var(--primary);
    cursor: pointer;
  }
  .main.active{
    display: block;
  }

  .container {
    position: absolute;
    display: flex;
    justify-content: center;
    
    width: 100%;
    padding-top: 60px;
    padding-bottom: 100px;
    /* background-color: rgba(0,0,0,0.2); */
    z-index: 9;
  }
  .container_1 {
    
    display: flex;
    justify-content: center;
    
    width: 100%;
    padding-top: 60px;
    padding-bottom: 100px;
    /* background-color: rgba(0,0,0,0.2); */
    z-index: 9;
  }
  
  .frame-short {
    height:510px;
    width: 430px;
    background: linear-gradient( rgb(133 143 198), rgb(65 78 145));
   margin: 10px;
    border-top: solid 1px rgba(255,255,255,.5);
    border-radius: 5px;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.2);
    overflow: hidden;
    transition: all .5s ease;
    animation: login 1s;
  }
  @keyframes login{
    from{
      transform: scale(0);
    }
    
    to{
      transform: scale(1);
    }

  }
  @-webkit-keyframes login {
    from {-webkit-transform: scale(0)} 
    to {-webkit-transform: scale(1)}
  }
  
  .frame-long {
    height: 730px;
    width: 430px;
    background:white;
    
    margin-left: auto;
    margin-right: auto;
    border-top: solid 1px rgba(255,255,255,.5);
    border-radius: 5px;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.2);
    overflow: hidden;
    transition: all .5s ease;
  }
  
  /* .frame-short {
    height: 400px;
    margin-top: 50px;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.1);
  } */
  
  .nav {
    width: 100%;
    height: 100px;
    padding-top: 40px;
    opacity: 1;
    transition: all .5s ease;
  }
  
  .nav-up {
    transform: translateY(-100px);
    opacity: 0;
  }

  .links{
    display: flex;
    animation: link-animation 1.4s reverse;
  }

  @keyframes link-animation{
    form{
      margin-top: 0px;
    }
    to{
      margin-top: -300px;
    }
  }


.links div{
    padding-left: 10px;
    font-size: 18px;
    display: inline;
    text-align: left;
    text-transform: uppercase;
    padding-right: 10px;
    color: rgb(255, 255, 255);
}

.form_body{
  height: 400px;
  position: relative;
  box-sizing: border-box;
  margin: 0px 15px;
  border-radius: 7px 7px 7px 7px;
  /* margin: 20px auto 50px auto ; */
  background-color: rgb(0,0,0,0.2);
}

.donate-active span , .require-active span {
    padding-bottom: 10px;
    color:black;
    text-decoration: none;
    border-bottom: solid 2px var(--primary);
    transition: all .25s ease;
    /* cursor: pointer; */
  }

  .donate-form , .accept-form {
    width: 430px;
    /* height: 375px; */
    height: auto;
    font-size: 16px;
    font-weight: 300;
    padding-left: 37px;
    padding-right: 37px;
    padding-top: 15px;
    position: relative;
    /* top: -35px; */
    /* left: 400px; */
    /* left: 0px; */
    animation: donate-form-animation 1.1s reverse;
    opacity: 1;
    transition: all 1.2s ease;
  }

  @keyframes donate-form-animation {
    form {
        /* padding-left: 0px; */
        margin-left: 0px;
    }
  
    to {
      /* padding-left: 400px; */
      margin-left: 400px;
    }
  
  }
  
  .donate-form-left {
    transform: translateX(-399px);
    opacity: 1;
  }
  
  .donate-form-down {
    top: 0px;
    opacity: 0;
  }

  .form-signin input, .donate-form input {
    color:black;
    font-size: 13px;
    font-weight: bold;
  }
  
  .form-styling {
    width: 100%;
    height: 35px;
    /* padding-left: 15px; */
    border: none;
    
    padding: 10px;
    border-radius: 18px;
    margin-bottom: 20px;
    background: rgb(251, 239, 239);
  }

  .form-styling-file{
    width: 100%;
    padding: 15px;
    border-radius: 30px;
    margin-bottom: 20px;
    background: rgba(255,255,255,.2);
  }
  
  label {
    font-weight: 400;
    text-transform: uppercase;
    font-size: 13px;
    padding-left: 15px;
    padding-bottom: 10px;
    color: black;
    display: block;
  }
  .container label{
    color: white;
  }
  
  .bookAppBtn, .getBloodBtn, .signIn_btn, .signUp_btn{
    float: left;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 13px;
    text-align: center;
    color: white;
    padding: 5px;
    width: 100%;
    height: 35px;
    border: none;
    border-radius: 5px;
    margin-top: 23px;
    background-color: var(--primary);
  }
  
  
  .bookAppBtn:hover, .getBloodBtn:hover, .signIn_btn:hover, .signUp_btn:hover{
      cursor: pointer; 
      background-color:var(--primary);
      transition: background-color .5s; 
  }

  h1 {
    color: rgb(255, 255, 255);
    font-size: 35px;
      font-weight: 300;
    text-align: center;
  }
  .bufferBlock{
    position: fixed;
    height: 100vh;
    width: 100vw;
    z-index: 6;
}

.bufferIcon{
    height:90px;
    width: 90px;
    margin-left: 46vw;
    margin-right: auto;
    margin-top: 20%;
    /* background: url("images/buff1.png") no-repeat fixed center; */
}
