@keyframes rotateBuffer{
    from{
        transform: rotate(360deg);
    }

    to{
        transform: rotate(0deg);
    }
}


.popupMsg {
    position: absolute;
    text-align: center;
    height: 50vh;
    width: 100%;
    /* margin-left: 45%; */
    margin-top: 400px;
  }
  
  .popupMsgBox {
    position: absolute;
    text-align: center;
    margin-left: 45%;
    padding: 5px;
    margin-right: auto;
    border-radius: 30px;
    height: 50px;
    max-height: 52px;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    z-index: 6;
    /* bottom: 100px; */
    transition-duration: 1s;
    transition-delay: 1s;
    animation: popupMsgBox 1.2s reverse;
  }
  
  @keyframes popupMsgBox {
    form {
      height: 10px;
    }
  
    to {
      margin-top: 80vh;
      height: 10px;
    }
  }

  .popupMsgBox p{
    margin: 0;
    padding: 8px;
  }




