    opacity: 1;
    transition: all 1.2s ease; 
  }

  
  .signUp-form{
    width: 100%;
    /* height: 375px; */
    height: auto;
    font-size: 16px;
    font-weight: 300px;
    padding: 37px;
  
    position: absolute;
    top: 0;
    left: 400px;    
    animation: signup-form-animation 1.1s reverse;
    opacity: 1;
    transition: all 1.2s ease; 
  }

  .forgot {
    height: 80px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    padding-top: 15px;
    margin-top: -70px;
    border-top: solid 1px rgba(255,255,255,.3);
    transition: all 0.5s ease;
  }
  
  .forgot-left {
    transform: translateX(-400px);
    opacity: 0;
  }
  
  .forgot-fade {
    opacity: 0;
  }
 #snackbar,#snackbar2, #snackbar1 , #snackbar5 ,#snackbar3{
    visibility: hidden;
    min-width: 250px;
    margin-left: -125px;
    background-color: #333;
    color: #fff;
    text-align: center;
    border-radius: 2px;
    padding: 16px;
    position: fixed;
    z-index: 1;
    left: 50%;
    bottom: 30px;
    font-size: 17px;
  }
  #snackbar1.show ,#snackbar5.show ,#snackbar3.show,#snackbar.show{
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
  }
  
  .forgot a {
    color: rgba(255,255,255,.3);
    font-weight: 400;
    font-size: 13px;
    text-decoration: none;
  }
  @media  (max-width:450px){
    
    .frame-long{
        width: 100%;

    }
    .nav{
      height: 80px;
        padding-top: 20px;
    }
    .donate-form {
        padding-left: 15px;
    }
    .bookAppBtn{
        width: 50%;

    }

    
  }
  
#snackbar , #snackbar3 ,#snackbar5,#snackbar6{
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}
#snackbar.show ,#snackbar3.show ,#snackbar5.show,#snackbar6.show{
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}