header {
  position: relative;

  /* top: -50px; */
  height: 500px;
  width: 100%;
  border-bottom: 1px solid #dce1e4;
  background: linear-gradient(rgba(1, 1, 1, 0.5), rgba(243, 4, 4, 0.5)),url(https://images.pexels.com/photos/12193105/pexels-photo-12193105.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);

  /* background-image: 100%; */
  background-position: cover;
  background-repeat: none;
}
.navbar {
  display: flex;
  background-color: white;
  border-bottom: 0.2px solid #ccc;

  z-index: 2;
  position: relative;
  /* box-shadow: 0 0 10px #0009; */
 
  width: 100%;
  height: 50px;

  justify-content: flex-end;
}
.navbar a {
  padding: 10px;
  margin: 10px;
  position: relative;
  font-family: Poppins;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  color: black;
  font-weight: 400;

  
}
.navbar a::after {
  content: "";
  position: absolute;
  background: white;

  width: 0;
  height: 3px;
  left: 6px;
  border-radius: 5px;
  bottom: -3px;
  transition: 0.5s;
}
.navbar i {
  display: none;
  padding: 10px 16px;
}
.navbar .fa-bars {
  color: black;
  
  position: relative;
  right: 14px;
  top: 6px;
  font-size: 22px;
}
.navbar a:hover::after {
  width: 80%;
}

