.sticky {
  position: fixed;
  top: 0;
  background-color: white;
  width: 100%;
  
  color: #111 !important;
  border-bottom: 1px solid #ccc;
  
  z-index: 100;
  animation: amination2 1s;
}
@keyframes amination2 {
  from {
    margin-top: -40px;
  }
  to {
    margin-top: 0;
  }
}
@-webkit-keyframes amination1 {
  from {
    margin-top: 40px;
  }
  to {
    margin-top: 0px;
  }
}
