.sidebar {
  height: 50%;
  width: 0;
  position: fixed;
  z-index: 99;
  top: 10px;
  right: 0;
  box-shadow: 0 0 2px #0009;
  background-color: white;
  overflow: hidden;
  transition: 0.5s;
  padding-top: 6px;
}
.sidebar.active {
  width: 220px;
  transition: 0.5s;
}
.sidebar a {
  position: relative;
  border-collapse: collapse;

  top:33px;
 
  text-transform: uppercase;
  padding: 10px 10px 10px 20px;
  text-decoration: none;
  font-size: 14px;
  color: #111;
  display: block;
  transition: 0.3s;
  
 
}
.sidebar a:nth-child(even){
  background-color: rgb(248, 246, 246);

}
.sidebar a:hover {
  color: rgb(243, 160, 7);
  transition: 0.1s;
  -webkit-transition: 0.1s;
  -moz-transition: 0.1s;
}
.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}

